// ====================ORDER=============================

.order {
  // .order__go-back

  &__go-back {
  }

  // .order__title-label

  &__title-label {
  }

  // .order__content-box

  &__content-box {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 40px;
    margin-bottom: 40px;
    @media (max-width: 1200px) {
      grid-template-columns: repeat(1, 1fr);
    }
  }

  // .order__item

  &__item {
    display: flex;
    flex-direction: column;
    row-gap: 20px;
    // max-width: 354px;
  }

  // .order__input-block

  &__input-block {
    display: flex;
    gap: 20px;
  }

  &__input {
    width: 100%;
    background: #ffffff;
    border-radius: 5px;
    padding-top: 9px;
    padding-bottom: 9px;
    padding-left: 10px;
    font-family: "DINPro";
    font-style: normal;
    font-weight: 400;
    font-size: 18px;
    line-height: 1.25;
    color: #111111;
    &:focus {
      outline: 0.5px solid rgba(164, 172, 173, 0.2);
    }
  }

  // .order__radio-items

  &__radio-items {
    display: flex;
    width: 100%;
    gap: 20px;
  }

  // .order__radio-item

  &__radio-item {
    display: flex;
    align-items: center;
    width: 100%;
    padding-top: 9px;
    padding-bottom: 9px;
    justify-content: center;
    background-color: #ffffff;
    border-radius: 5px 5px 5px 5px;
    color: #111111;
    cursor: pointer;
    gap: 10px;
    transition: all 0.3s ease 0s;

    &:focus {
      background-color: #ff9846;
      color: #ffffff;
      svg path {
        fill: white;
      }
    }
  }

  // .order__radio-item-text

  &__radio-item-text {
    font-family: "DINPro";
    font-size: 18px;
    line-height: 1.25;
  }
  &__comment {
    font-family: "DINPro";
    font-style: normal;
    font-weight: 400;
    font-size: 18px;
    line-height: 1.25;
    border-radius: 5px;
    padding-top: 9px;
    padding-bottom: 9px;
    padding-left: 10px;
    resize: none;
    &:focus {
      outline: 0.5px solid rgba(164, 172, 173, 0.2);
    }
  }

  // .order__email

  &__email {
    width: 100%;
    background: #ffffff;
    border-radius: 5px;
    padding-top: 9px;
    padding-bottom: 9px;
    padding-left: 10px;
    font-family: "DINPro";
    font-style: normal;
    font-weight: 400;
    font-size: 18px;
    line-height: 1.25;
    color: #111111;
    &:focus {
      outline: 0.5px solid rgba(164, 172, 173, 0.2);
    }
  }
}
